import { Component, Input } from '@angular/core';
import { MatDialogRef } from '@angular/material';
import { ThemeService } from '../../theme/theme.service';
import { RadioDialogValue } from './radio-dialog.model';

@Component({
  selector: 'frigg-radio-dialog',
  templateUrl: 'radio-dialog.component.html',
  styleUrls: [ 'radio-dialog.component.scss' ]
})
export class RadioDialogComponent {
  @Input() message: string;
  @Input() selectedValue: any;
  @Input() radioDialogValues: RadioDialogValue[];

  constructor(public themeService: ThemeService,
              public dialogRef: MatDialogRef<RadioDialogComponent>) {
  }
}




