// The file contents for the current environment will overwrite these during build.
// The build system defaults to the dev environment which uses `environment.ts`, but if you do
// `ng build --env=prod` then `environment.prod.ts` will be used instead.
// The list of which env maps to which file can be found in `.angular-cli.json`.

import { AuthConfig } from 'angular-oauth2-oidc';

const hostName: string = window.location.hostname;

const authConfig: AuthConfig = {
  issuer: `http://${hostName}:5000`,
  logoutUrl: `http://${hostName}:5000/identity/account/logout`,
  postLogoutRedirectUri: window.location.origin,
  redirectUri: window.location.origin + '/login_callback',
  clientId: 'cplan',
  scope: 'openid profile default-api'
};

export const environment = {
  production: false,
  isCordova: false,
  apiPath: 'http://localhost:5000',
  authStorage: localStorage,
  appInsightInstrumentationKey: '',
  authConfig
};



