import { InjectionToken } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { NotificationMessage } from './notification-message';

export const NotificationService = new InjectionToken<INotificationService>('NotificationService');

export interface INotificationService {
  listen(subject: string, event: string): Observable<NotificationMessage<any>>;

  stopListening(subject: string): void;
}



