import { Component, ContentChild, EventEmitter, Input, Output, TemplateRef, ViewEncapsulation } from '@angular/core';

@Component({
  selector: 'frigg-tile-list',
  templateUrl: 'tile-list.component.html',
  styleUrls: [ 'tile-list.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class TileListComponent {
  @Input() items: any[] = [];
  @Input() showHeader = true;
  @Input() header: string;
  @Input() showCount = true;
  @Input() allowAdd = true;
  @Input() allowDelete = true;
  @Output() addItemClick: EventEmitter<any> = new EventEmitter();
  @Output() itemClick: EventEmitter<any> = new EventEmitter();
  @Output() deleteItemClick: EventEmitter<any> = new EventEmitter();
  @ContentChild(TemplateRef) itemTemplate: TemplateRef<any>;

  onAddItemClick(): void {
    this.addItemClick.next();
  }

  onItemClick(item: any): void {
    if (!item.deleteFlag) {
      this.itemClick.next(item);
    }
  }

  onItemDeleteClick(item: any): void {
    this.deleteItemClick.next(item);
  }

  scrollInto(item: any): void {
    // TODO: Scroll to specified item in list
  }
}



