import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ValidationSchema } from 'class-validator';
import { Observable } from 'rxjs/Observable';
import { ListResultDto } from '../frigg-response.model';
import { FriggResponse, FriggServiceHandler } from '../frigg.service.handler';

@Injectable()
export class RoleService {
  constructor(private serviceHandler: FriggServiceHandler) {
  }

  getRoleList(): Observable<FriggResponse<ListResultDto<RoleListDto>>> {
    return this.serviceHandler.request('GET', 'api/services/app/Role/GetRoleList',
      {});
  }

  getRole(roleId: Number): Observable<FriggResponse<RoleDto>> {
    return this.serviceHandler.request('GET', 'api/services/app/Role/GetRole?roleId= ' + encodeURI(roleId.toString())
      + '',
      {});
  }

  createRole(input: RoleDto): Observable<FriggResponse<RoleDto>> {
    return this.serviceHandler.request('POST', 'api/services/app/Role/CreateRole',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify(input)
      });
  }

  updateRole(input: RoleDto): Observable<FriggResponse<RoleDto>> {
    return this.serviceHandler.request('PUT', 'api/services/app/Role/UpdateRole',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify(input)
      });
  }

  deleteRole(roleId: Number): Observable<FriggResponse<boolean>> {
    return this.serviceHandler.request('DELETE', 'api/services/app/Role/DeleteRole?roleId= ' + encodeURI(roleId.toString())
      + '',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify({})
      });
  }

  updateRolePermissions(input: UpdateRolePermissionsInput): Observable<FriggResponse<void>> {
    return this.serviceHandler.request('PUT', 'api/services/app/Role/UpdateRolePermissions', {
      headers: new HttpHeaders({'Content-type': 'application/json'}),
      body: JSON.stringify(input)
    });
  }
}

export class RoleDto {
  name: string;
  displayName: string;
  isStatic: boolean;
  isDefault: boolean;
  id: number;
}

export let RoleDtoSchema: ValidationSchema = {
  name: 'RoleDtoSchema',
  properties: {
    name: [
      {
        type: 'maxLength',
        constraints: [ 32 ],
        options: []
      }

    ]
    , displayName: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }
      , {
        type: 'maxLength',
        constraints: [ 64 ],
        options: []
      }

    ]

  }
};


export class UpdateRolePermissionsInput {
  roleId: number;
  grantedPermissionNames: string[];
}

export let UpdateRolePermissionsInputSchema: ValidationSchema = {
  name: 'UpdateRolePermissionsInputSchema',
  properties: {
    roleId: [
      {
        type: 'min',
        constraints: [ 1 ],
        options: []
      }
      , {
        type: 'max',
        constraints: [ 2147483647 ],
        options: []
      }

    ]
    , grantedPermissionNames: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }

    ]

  }
};


export class RoleListDto {
  displayName: string;
  isStatic: boolean;
  isDefault: boolean;
  id: number;
}







