import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { SelectModule } from 'ng-select';
import { FriggMaterialModule } from '../frigg-material/frigg-material.module';
import { InputComponent } from './input/input.component';
import { FriggSelectComponent } from './select/select.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    FriggMaterialModule,
    SelectModule
  ],
  exports: [
    InputComponent,
    FriggSelectComponent
  ],
  declarations: [
    InputComponent,
    FriggSelectComponent
  ]
})
export class ControlsModule {
}



