/* tslint:disable:adjacent-overload-signatures */
import {
  Component,
  DoCheck,
  ElementRef,
  EventEmitter,
  forwardRef,
  HostBinding,
  Input,
  OnChanges,
  Output,
  Renderer2,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { MatFormField } from '@angular/material';
import { IValidationComponent } from '../../directives/validation/validation.directive';

const noop: () => void = (): void => {
};
let nextId = 0;

export const IS_FOCUSED = 'is-focused';
export const IS_DISABLED = 'is-disabled';
export const IS_INVALID = 'is-invalid';
export const IS_DIRTY = 'is-dirty';

@Component({
  selector: 'frigg-input',
  templateUrl: 'input.component.html',
  styleUrls: [ 'input.component.scss' ],
  providers: [ {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => InputComponent),
    multi: true
  } ],
  encapsulation: ViewEncapsulation.None
})
export class InputComponent implements ControlValueAccessor, OnChanges, DoCheck, IValidationComponent {
  private value_: any;
  private el: HTMLElement;

  private onTouchedCallback: () => void = noop;
  private onChangeCallback: (_: any) => void = noop;

  @HostBinding('class.ng-valid') isValid: boolean;
  @HostBinding('class.ng-invalid') isInvalid: boolean;

  @Output()
  blur: EventEmitter<FocusEvent> = new EventEmitter<FocusEvent>();

  @Output()
  focus: EventEmitter<FocusEvent> = new EventEmitter<FocusEvent>();

  @Output('keyup')
  keyup: EventEmitter<KeyboardEvent> = new EventEmitter<KeyboardEvent>();

  @ViewChild('inputElement') inputElement: ElementRef;
  @ViewChild('inputContainer') inputContainer: MatFormField;

  @Input() type = 'text';
  @Input() label: string;
  @Input() pattern: string;
  @Input() min: string;
  @Input() max: string;
  @Input() step: string;
  @Input() name: string;
  @Input() id = `frigg-input-${nextId++}`;
  @Input() errorMessage: string;

  get value(): any {
    return this.value_;
  };

  @Input() set value(v: any) {
    this.value_ = v;
    this.onChangeCallback(this.value);
  }

  get validationElement(): ElementRef {
    return this.inputElement;
  }


  private _disabled = false;

  @Input()
  get disabled(): boolean {
    return this._disabled;
  }

  set disabled(value: boolean) {
    this._disabled = this.toBoolean(value);
  }

  private _readonly = false;
  @Input()
  get readonly(): boolean {
    return this._readonly;
  }

  set readonly(value: boolean) {
    this._readonly = this.toBoolean(value);
  }

  private _required = false;
  @Input()
  get required(): boolean {
    return this._required;
  }

  set required(value: boolean) {
    this._required = this.toBoolean(value);
  }

  private _autofocus = false;
  @Input()
  get autofocus(): boolean {
    return this._autofocus;
  }

  set autofocus(value: boolean) {
    this._autofocus = this.toBoolean(value);
  }

  @Input() autocomplete: string;

  @Input() tabindex: number;

  @Input() maxlength: number;

  constructor(private renderer: Renderer2,
              private elmRef: ElementRef) {
    this.el = elmRef.nativeElement;
  }

  writeValue(value: any): void {
    this.value_ = value;
    this.checkDirty();
  }

  registerOnChange(fn: any): void {
    this.onChangeCallback = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouchedCallback = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  ngOnChanges(changes: any): void {
    this.checkDisabled();
  }

  ngDoCheck(): void {
    this.checkValidity();
    this.checkDirty();
  }

  setFocus(): void {
    if (this.inputElement) {
      this.inputElement.nativeElement.focus();
    }
  }

  onFocus(event: FocusEvent): void {
    this.renderer.addClass(this.el, IS_FOCUSED);
    this.focus.emit(event);
  }

  onBlur(event: FocusEvent): void {
    this.renderer.removeClass(this.el, IS_FOCUSED);
    this.onTouchedCallback();
    this.blur.emit(event);
  }

  onKeyup(event: KeyboardEvent): void {
    this.keyup.emit(event);
  }

  private checkDisabled(): void {
    this.disabled ?
      this.renderer.addClass(this.el, IS_DISABLED) :
      this.renderer.removeClass(this.el, IS_DISABLED);
  }

  private checkValidity(): void {
    if (this.inputElement && this.inputElement.nativeElement.validity) {
      this.inputElement.nativeElement.validity.valid ?
        this.renderer.removeClass(this.el, IS_INVALID) :
        this.renderer.addClass(this.el, IS_INVALID);
    }
  }

  private checkDirty(): void {
    const dirty: boolean = this.inputElement && this.inputElement.nativeElement.value && this.inputElement.nativeElement.value.length > 0;
    dirty ?
      this.renderer.addClass(this.el, IS_DIRTY) :
      this.renderer.removeClass(this.el, IS_DIRTY);
  }

  private toBoolean(value: any): boolean {
    return value != null && `${value}` !== 'false';
  }

}



