import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { TranslateLoader } from '@ngx-translate/core';
import { Observable } from 'rxjs/Observable';
import { environment } from '../../../environments/environment';
import { FriggResponse } from '../../proxies';

@Injectable()
export class FriggTranslationLoader implements TranslateLoader {

  constructor(private httpClient: HttpClient) {
  }

  getTranslation(lang: string): Observable<any> {
    return this.httpClient
      .get(`${environment.apiPath}/api/services/app/Localization/GetTitles?locale=${lang}`)
      .map((friggResponse: FriggResponse<any>) => friggResponse.result);
  }

}



