import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { ListResultDto } from '../frigg-response.model';
import { FriggResponse, FriggServiceHandler } from '../frigg.service.handler';

@Injectable()
export class TaskService {
  constructor(private serviceHandler: FriggServiceHandler) {
  }

  getMyTaskList(): Observable<FriggResponse<ListResultDto<TaskListDto>>> {
    return this.serviceHandler.request('GET', 'api/services/cplan/Task/GetMyTaskList',
      {});
  }

  getActiveTasks(): Observable<FriggResponse<ListResultDto<TaskListDto>>> {
    return this.serviceHandler.request('GET', 'api/services/cplan/Task/GetActiveTasks',
      {});
  }

  getTask(jobId: Number, taskId: Number): Observable<FriggResponse<TaskDto>> {
    return this.serviceHandler.request('GET', 'api/services/cplan/Task/GetTask?jobId= ' + encodeURI(jobId.toString())
      + '&taskId= ' + encodeURI(taskId.toString())
      + '',
      {});
  }

  executeUploadAction(input: ExecuteUploadActionInput): Observable<FriggResponse<TaskDto>> {
    return this.serviceHandler.request('POST', 'api/services/cplan/Task/ExecuteUploadAction',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify(input)
      });
  }

  executeAction(input: ExecuteActionInput): Observable<FriggResponse<TaskDto>> {
    return this.serviceHandler.request('POST', 'api/services/cplan/Task/ExecuteAction',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify(input)
      });
  }

  commentEvent(input: CommentEventInput): Observable<FriggResponse<TaskDto>> {
    return this.serviceHandler.request('POST', 'api/services/cplan/Task/CommentEvent',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify(input)
      });
  }
}

export class TaskDto {
  id: number;
  name: string;
  description: string;
  status: string;
  dueDate: Date;
  active: boolean;
  job: TaskJobDto;
  actions: TaskActionDto[];
}

export class TaskJobDto {
  name: string;
  description: string;
  status: string;
  id: number;
}

export class ExecuteUploadActionInput {
  fileContent: string;
  fileName: string;
  fileDate: Date;
  jobId: number;
  taskId: number;
  actionId: number;
  comment: string;
}

export class ExecuteActionInput {
  jobId: number;
  taskId: number;
  actionId: number;
  comment: string;
}

export class CommentEventInput {
  jobId: number;
  taskId: number;
  eventId: number;
  comment: string;
}

export class TaskListDto {
  id: number;
  name: string;
  description: string;
  status: string;
  dueDate: Date;
  active: boolean;
  job: TaskListJobDto;
}

export class TaskListJobDto {
  id: number;
  name: string;
}

export class TaskActionDto {
  name: string;
  description: string;
  actionType: string;
  requiredAction: boolean;
  actionComment: number;
  enabled: boolean;
  actionCount: number;
  parameters: TaskActionParameterDto[];
  document: TaskActionDocumentDto;
  id: number;
}

export class TaskActionParameterDto {
  description: string;
  value: string;
  id: number;
}

export class TaskActionDocumentDto {
  name: string;
  description: string;
  documentType: string;
  id: number;
}







