import { ApplicationRef, enableProdMode } from '@angular/core';
import { disableDebugTools, enableDebugTools } from '@angular/platform-browser';
import { environment } from '../environments/environment';

// Environment Providers
let PROVIDERS: any[] = [
  // common env directives
];

// Angular debug tools in the dev console
// https://github.com/angular/angular/blob/86405345b781a9dc2438c0fbe3e9409245647019/TOOLS_JS.md
let _decorateModuleRef: <T>(value: T) => any = <T>(value: T): T => {
  return value;
};

if (environment.production) {
  enableProdMode();

  // Production
  _decorateModuleRef = (modRef: any): any => {
    disableDebugTools();

    return modRef;
  };

  PROVIDERS = [
    ...PROVIDERS,
    // custom providers in production
  ];

} else {
  _decorateModuleRef = (modRef: any): any => {
    const appRef: any = modRef.injector.get(ApplicationRef);
    const cmpRef: any = appRef.components[ 0 ];

    const _ng: any = (<any> window).ng;
    enableDebugTools(cmpRef);
    (<any> window).ng.probe = _ng.probe;
    (<any> window).ng.coreTokens = _ng.coreTokens;
    return modRef;
  };

  // Development
  PROVIDERS = [
    ...PROVIDERS,
    // custom providers in development
  ];

}

export const decorateModuleRef: any = _decorateModuleRef;

export const ENV_PROVIDERS: Array<any> = [
  ...PROVIDERS
];



