import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { FriggResponse, FriggServiceHandler } from '../frigg.service.handler';

@Injectable()
export class AuthorizationService {
  constructor(private serviceHandler: FriggServiceHandler) {
  }

  authorize(channel_name: String, socket_id: String): Observable<FriggResponse<AuthorizationDto>> {
    return this.serviceHandler
      .request('POST', 'api/services/pusher/Authorization/Authorize?channel_name= ' + encodeURI(channel_name.toString())
        + '&socket_id= ' + encodeURI(socket_id.toString())
        + '',
        {
          headers: new HttpHeaders({'Content-type': 'application/json'}),
          body: JSON.stringify({})
        });
  }
}

export class AuthorizationDto {
  auth: string;
}







