import { Component, Input, ViewEncapsulation } from '@angular/core';
import { MatDialogRef } from '@angular/material';
import { ThemeService } from '../../theme/theme.service';

@Component({
  selector: 'frigg-execute-action-dialog',
  templateUrl: 'execute-action-dialog.component.html',
  styleUrls: [ 'execute-action-dialog.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class ExecuteActionDialogComponent {

  @Input() message: string;
  @Input() doneText = 'Ok';

  comment = '';
  placeholder = '';
  canComment = true;
  required = false;

  constructor(public themeService: ThemeService,
              public dialogRef: MatDialogRef<ExecuteActionDialogComponent>) {
  }

  get canExecute(): boolean {
    return !this.required || (this.required && this.comment.trim().length > 0)
  }
}



