import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class ShellService {
  header: string;
  navigateText: string;
  navigateUrl: string;
  lockedSideNav: boolean;

  constructor(private router: Router) {
  }

  setHeader(header: string | Observable<string>): void {
    if (header instanceof Observable) {
      header.subscribe((newHeader: string) => {
        this.header = newHeader;
      });
    } else {
      this.header = header;
    }
  }

  setNavigateText(text: string | Observable<string>): void {
    if (text instanceof Observable) {
      text.subscribe((newText: string) => {
        this.navigateText = newText;
      });
    } else {
      this.navigateText = text;
    }
  }

  setNavigateUrl(navigateUrl: string): void {
    this.navigateUrl = navigateUrl;
  }

  navigateBack(): void {
    this.router.navigate([ this.navigateUrl ]);
  }

}



