import { Injectable } from '@angular/core';

export type LoadingState = 'started' | 'stopped' | 'completed';

@Injectable()
export class LoadingService {
  state: LoadingState = 'completed';

  start(): void {
    this.state = 'started';
  }

  stop(): void {
    this.state = 'stopped';
  }

  complete(): void {
    this.state = 'completed';
  }
}



