import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { ListResultDto } from '../frigg-response.model';
import { FriggResponse, FriggServiceHandler } from '../frigg.service.handler';
import { JobDefinitionDto } from './jobDefinition.service.g';

@Injectable()
export class JobService {
  constructor(private serviceHandler: FriggServiceHandler) {
  }

  activateJob(jobId: Number): Observable<FriggResponse<JobDefinitionDto>> {
    return this.serviceHandler.request('POST', 'api/services/cplan/Job/ActivateJob?jobId= ' + encodeURI(jobId.toString())
      + '',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify({})
      });
  }

  pauseJob(jobId: Number): Observable<FriggResponse<JobDefinitionDto>> {
    return this.serviceHandler.request('POST', 'api/services/cplan/Job/PauseJob?jobId= ' + encodeURI(jobId.toString())
      + '',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify({})
      });
  }

  unPauseJob(jobId: Number): Observable<FriggResponse<JobDefinitionDto>> {
    return this.serviceHandler.request('POST', 'api/services/cplan/Job/UnPauseJob?jobId= ' + encodeURI(jobId.toString())
      + '',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify({})
      });
  }

  restartJob(jobId: Number): Observable<FriggResponse<JobDefinitionDto>> {
    return this.serviceHandler.request('POST', 'api/services/cplan/Job/RestartJob?jobId= ' + encodeURI(jobId.toString())
      + '',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify({})
      });
  }

  abortJob(jobId: Number): Observable<FriggResponse<JobDefinitionDto>> {
    return this.serviceHandler.request('POST', 'api/services/cplan/Job/AbortJob?jobId= ' + encodeURI(jobId.toString())
      + '',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify({})
      });
  }

  getEventsWithComments(jobId: Number): Observable<FriggResponse<ListResultDto<EventDto>>> {
    return this.serviceHandler.request('GET', 'api/services/cplan/Job/GetEventsWithComments?jobId= ' + encodeURI(jobId.toString())
      + '',
      {});
  }

  getEvent(jobId: Number, eventId: Number): Observable<FriggResponse<EventDto>> {
    return this.serviceHandler.request('GET', 'api/services/cplan/Job/GetEvent?jobId= ' + encodeURI(jobId.toString())
      + '&eventId= ' + encodeURI(eventId.toString())
      + '',
      {});
  }
}

export class EventDto {
  id: number;
  sequenceNr: number;
  taskId: number;
  actionId: number;
  command: string;
  comments: CommentDto[];
  creationTime: Date;
  creatorUser: EventUserDto;
  creatorUserId: number;
}

export class EventUserDto {
  name: string;
  surname: string;
  userName: string;
  emailAddress: string;
  imageUrl: string;
}

export class CommentDto {
  creationTime: Date;
  creatorUser: CommentUserDto;
  creatorUserId: number;
  comment: string;
}

export class CommentUserDto {
  name: string;
  surname: string;
  userName: string;
  emailAddress: string;
  imageUrl: string;
}







