import { Component, ContentChild, EventEmitter, Input, Output, TemplateRef, ViewChild } from '@angular/core';
import { TileListComponent } from '../../tile-list/tile-list/tile-list.component';

@Component({
  selector: 'frigg-header-card-tile-list',
  templateUrl: 'header-card-tile-list.component.html',
  styleUrls: [ 'header-card-tile-list.component.scss' ]
})
export class HeaderCardTileListComponent {
  @Input() showListHeader = true;
  @Input() listHeader = '-';
  @Input() itemsSource: any = [];
  @Input() addNewHref: string;
  @Input() addNewQueryParams: any = {};
  @Input() filterFields: string[] = [];

  @ViewChild('tileList') tileList: TileListComponent;
  @Output() itemClick: EventEmitter<any> = new EventEmitter();
  @ContentChild(TemplateRef) itemTemplate: TemplateRef<any>;

  toFilter: any;
  filterValue = '';


  onItemClick(item: any): void {
    this.itemClick.next(item);
  }

  get filteredItems(): any {
    return this.itemsSource;
  }

  get filter(): string {
    return this.filterValue;
  }

  set filter(value: string) {
    if (this.filterValue !== value) {
      this.filterValue = value;
    }
  }

  scrollInto(item: any): void {
    this.tileList.scrollInto(item);
  }
}



