import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { FriggResponse, FriggServiceHandler } from '../frigg.service.handler';

@Injectable()
export class DocumentService {
  constructor(private serviceHandler: FriggServiceHandler) {
  }

  getDocumentRevision(jobId: Number, documentId: Number, revisionId: Number): Observable<FriggResponse<GetDocumentDto>> {
    return this.serviceHandler.request('GET', 'api/services/cplan/Document/GetDocumentRevision?jobId= ' + encodeURI(jobId.toString())
      + '&documentId= ' + encodeURI(documentId.toString())
      + '&revisionId= ' + encodeURI(revisionId.toString())
      + '',
      {});
  }

  getLatestDocumentRevision(jobId: Number, documentId: Number): Observable<FriggResponse<GetDocumentDto>> {
    return this.serviceHandler.request('GET', 'api/services/cplan/Document/GetLatestDocumentRevision?jobId= ' + encodeURI(jobId.toString())
      + '&documentId= ' + encodeURI(documentId.toString())
      + '',
      {});
  }

  getFileContentResult(jobId: Number, documentId: Number): Observable<FriggResponse<FileContentResult>> {
    return this.serviceHandler.request('GET', 'api/services/cplan/Document/{jobId}/{documentId}?jobId= ' + encodeURI(jobId.toString())
      + '&documentId= ' + encodeURI(documentId.toString())
      + '',
      {});
  }
}

export class GetDocumentDto {
  fileContent: string;
  fileName: string;
}

export class FileContentResult {
  fileContents: number[];
  contentType: string;
  fileDownloadName: string;
  lastModified: DateTimeOffset;
  entityTag: EntityTagHeaderValue;
}

export class DateTimeOffset {
  now: DateTimeOffset;
  utcNow: DateTimeOffset;
  dateTime: Date;
  utcDateTime: Date;
  localDateTime: Date;
  date: Date;
  day: number;
  dayOfWeek: number;
  dayOfYear: number;
  hour: number;
  millisecond: number;
  minute: number;
  month: number;
  offset: TimeSpan;
  second: number;
  ticks: number;
  utcTicks: number;
  timeOfDay: TimeSpan;
  year: number;
}

export class TimeSpan {
  ticks: number;
  days: number;
  hours: number;
  milliseconds: number;
  minutes: number;
  seconds: number;
  totalDays: number;
  totalHours: number;
  totalMilliseconds: number;
  totalMinutes: number;
  totalSeconds: number;
}

export class EntityTagHeaderValue {
  any: EntityTagHeaderValue;
  tag: StringSegment;
  isWeak: boolean;
}

export class StringSegment {
  buffer: string;
  offset: number;
  length: number;
  value: string;
  hasValue: boolean;
  item: Char;
}

export class Char {
}







