import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { VirtualScrollModule } from 'angular2-virtual-scroll';
import { FriggMaterialModule } from '../frigg-material/frigg-material.module';

import { TileListComponent } from './tile-list/tile-list.component';
import { VirtualTileListComponent } from './virtual-tile-list/virtual-tile-list.component';

@NgModule({
  imports: [
    CommonModule,
    VirtualScrollModule,
    FriggMaterialModule
  ],
  exports: [
    TileListComponent,
    VirtualTileListComponent
  ],
  declarations: [
    TileListComponent,
    VirtualTileListComponent
  ],
  providers: [],
})
export class TileListModule {
}




