import { Component, EventEmitter, Output, ViewEncapsulation } from '@angular/core';
import { CoreService } from '../../core/core.service';


@Component({
  selector: 'frigg-sidenav',
  templateUrl: 'sidenav.component.html',
  styleUrls: [ 'sidenav.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class SidenavComponent {
  static lockedStorageKey = 'locked-sidenav';
  static showLockedSideNavWidth = 700;

  @Output() toggleLock: EventEmitter<any> = new EventEmitter();
  @Output() navigateHome: EventEmitter<any> = new EventEmitter();
  @Output() navigate: EventEmitter<any> = new EventEmitter();

  isLocked: boolean;

  constructor(public core: CoreService) {
  }

  get lockIcon(): string {
    return this.isLocked ? 'lock' : 'lock_open';
  }

  onToggleLock(): void {
    this.isLocked = !this.isLocked;
    this.toggleLock.emit(this.isLocked);
    localStorage.setItem(SidenavComponent.lockedStorageKey, this.isLocked.toString());
  }

  onNavigate(url: string): void {
    this.navigate.emit({url});
  }

  onNavigateHome(): void {
    this.navigateHome.emit();
  }


  get showToolbarButtons(): boolean {
    return window.innerWidth > SidenavComponent.showLockedSideNavWidth;
  }
}



