import { Component, HostListener, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { MatSidenav } from '@angular/material';
import { Router } from '@angular/router';
import { CoreService } from '../../core/core.service';
import { ThemeService } from '../../core/theme/theme.service';
import { SidenavComponent } from '../sidenav/sidenav.component';

@Component({
  selector: 'frigg-shell',
  templateUrl: 'shell.component.html',
  styleUrls: [ 'shell.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class ShellComponent implements OnInit {
  static showLockedSideNavWidth = 700;

  @ViewChild('sideNav') sideNav: MatSidenav;
  @ViewChild('friggSideNav') friggSideNav: SidenavComponent;

  get lockedSideNav(): boolean {
    return this.core.shell.lockedSideNav;
  }

  set lockedSideNav(value: boolean) {
    this.core.shell.lockedSideNav = value;
  }

  constructor(public router: Router,
              private core: CoreService,
              public themeService: ThemeService) {
  }

  @HostListener('window:resize', [ '$event' ])
  onResize(e: Event): void {
    if (window.innerWidth < ShellComponent.showLockedSideNavWidth) {
      this.sideNav.close();
    } else if (this.core.shell.lockedSideNav && !this.sideNav.opened) {
      this.sideNav.open();
    }
  }

  ngOnInit(): void {
    if (localStorage.getItem(SidenavComponent.lockedStorageKey) === 'true') {
      this.friggSideNav.onToggleLock();
    }
  }

  get sideNavMode(): string {
    return this.lockedSideNav && window.innerWidth > ShellComponent.showLockedSideNavWidth
      ? 'side' : 'over';
  }

  onToggleLock(): void {
    this.lockedSideNav = !this.lockedSideNav;
    if (!this.lockedSideNav) {
      this.sideNav.close();
    }
    this.dispatchDelayedResize();
  }

  dispatchDelayedResize(): void {
    setTimeout(() => window.dispatchEvent(new Event('resize')), 50);
  }

  onNavigateHome(): void {
    this.router.navigate([ '' ]);
  }

  onNavigate(url: string): void {
    this.router.navigate([ url ]);
    if (!this.lockedSideNav || window.innerWidth < ShellComponent.showLockedSideNavWidth) {
      this.sideNav.close();
    }
  }
}



