import {
  AfterViewInit,
  Component,
  DoCheck,
  ElementRef,
  EventEmitter,
  forwardRef,
  HostBinding,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  Renderer2,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { IOption, SelectComponent } from 'ng-select';
import { Subscription } from 'rxjs/Subscription';
import { noop } from 'rxjs/util/noop';
import { IValidationComponent } from '../../directives/validation/validation.directive';

export const IS_FOCUSED = 'is-focused';
export const IS_DISABLED = 'is-disabled';
export const IS_INVALID = 'is-invalid';
export const IS_DIRTY = 'is-dirty';

@Component({
  selector: 'frigg-select',
  templateUrl: 'select.component.html',
  styleUrls: [ 'select.component.scss' ],
  providers: [ {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => FriggSelectComponent),
    multi: true
  } ],
  encapsulation: ViewEncapsulation.None
})
export class FriggSelectComponent implements ControlValueAccessor, OnChanges,
  OnInit, OnDestroy, AfterViewInit, DoCheck, IValidationComponent {
  subscriptions: Subscription[] = [];

  private value_: any;
  private hasFocus_: boolean;

  @Input() options: Array<IOption>;

  @Input() allowClear = false;
  @Input() highlightColor: string;
  @Input() highlightTextColor: string;
  @Input() multiple = false;
  @Input() noFilter = 0;
  @Input() notFoundMsg = 'No results found';
  @Input() placeholder = '';
  @Input() filterPlaceholder = '';
  @Input() label = '';
  @Input() errorMessage: string;

  // Placeholders for the callbacks which are later provided
  // by the Control Value Accessor
  private onTouchedCallback: () => void = noop;
  private onChangeCallback: (_: any) => void = noop;

  get value(): any {
    return this.value_;
  };

  @Input() set value(v: any) {
    if (v !== this.value_) {
      this.value_ = v;
      this.onChangeCallback(this.value);
      this.onTouchedCallback()
    }
  }

  private _disabled = false;

  @Input()
  get disabled(): boolean {
    return this._disabled;
  }

  set disabled(value: boolean) {
    this._disabled = this.toBoolean(value);
  }


  @HostBinding('class.is-focused')
  @HostBinding('class.mat-focused')
  get isFocused(): boolean {
    return this.hasFocus_ || this.ngSelect.isOpen;
  };

  @HostBinding('class.ng-valid') isValid: boolean;
  @HostBinding('class.ng-invalid') isInvalid: boolean;

  // @HostBinding('class.ng-touched')
  // get isTouched(): boolean {
  //   if (this.element.nativeElement === undefined) {
  //     return false;
  //   }
  // }

  // @HostBinding('class.ng-untouched')
  // get isUntouched(): boolean {
  //   return !this.isTouched;
  // };

  @Output() opened: EventEmitter<null> = new EventEmitter<null>();
  @Output() closed: EventEmitter<null> = new EventEmitter<null>();
  @Output() selected: EventEmitter<IOption> = new EventEmitter<IOption>();
  @Output() deselected: EventEmitter<IOption | IOption[]> =
    new EventEmitter<IOption | IOption[]>();
  @Output() noOptionsFound: EventEmitter<string> =
    new EventEmitter<string>();

  @ViewChild('ngSelect') ngSelect: SelectComponent;
  @ViewChild('dummyInput') dummyInput: ElementRef;


  // private ngSelectElement: HTMLElement;
  // private valueElement: HTMLDivElement;

  constructor(private element: ElementRef, private renderer: Renderer2) {
  }

  get validationElement(): ElementRef {
    return this.dummyInput;
  }

  ngAfterViewInit(): void {
    // setTimeout(() => {
    //   this.ngSelectElement = this.element.nativeElement.querySelector('ng-select') as HTMLElement;
    //   this.valueElement = this.element.nativeElement.querySelector('ng-select div') as HTMLDivElement;
    // });
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((sub: Subscription) => {
      sub.unsubscribe();
    })
  }

  ngOnInit(): void {
    this.ngSelect.ngOnInit();

    this.subscriptions.push(this.ngSelect.opened.subscribe(() => {
      this.opened.next();
    }));
    this.subscriptions.push(this.ngSelect.closed.subscribe(() => {
      this.closed.next();
    }));
    this.subscriptions.push(this.ngSelect.selected.subscribe((option: IOption) => {
      this.selected.next(option);
    }));
    this.subscriptions.push(this.ngSelect.deselected.subscribe((option: IOption) => {
      this.deselected.next(option);
    }));
  }

  onBlur(): void {
    this.hasFocus_ = false;
    this.onTouchedCallback();
  }

  onFocus(): void {
    this.hasFocus_ = true;
  }

  writeValue(value: any): void {
    this.value_ = value;
    this.checkDirty();
  }

  registerOnChange(fn: any): void {
    this.onChangeCallback = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouchedCallback = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  ngOnChanges(changes: any): void {
    this.ngSelect.ngOnChanges(changes);
    this.checkDisabled();
  }

  ngDoCheck(): void {
    this.checkValidity();
    this.checkDirty();
  }

  private checkDisabled(): void {
    this.disabled ?
      this.renderer.addClass(this.element.nativeElement, IS_DISABLED) :
      this.renderer.removeClass(this.element.nativeElement, IS_DISABLED);
  }

  private checkValidity(): void {
    if (this.element && this.element.nativeElement.validity) {
      this.element.nativeElement.validity.valid ?
        this.renderer.removeClass(this.element.nativeElement, IS_INVALID) :
        this.renderer.addClass(this.element.nativeElement, IS_INVALID);
    }
  }

  private checkDirty(): void {
    const dirty: boolean | string = this.ngSelect.value && this.ngSelect.value.length > 0;
    dirty ?
      this.renderer.addClass(this.element.nativeElement, IS_DIRTY) :
      this.renderer.removeClass(this.element.nativeElement, IS_DIRTY);
  }

  private toBoolean(value: any): boolean {
    return value != null && `${value}` !== 'false';
  }
}



