import { CommonModule } from '@angular/common';
import { ModuleWithProviders, NgModule, Optional, SkipSelf } from '@angular/core';

import { ApplicationInsightsConfig, ApplicationInsightsService } from './application-insights.service';

@NgModule({
  imports: [ CommonModule ],
  declarations: [],
  exports: [],
  providers: [ ApplicationInsightsService ]
})

export class ApplicationInsightsModule {
  static forRoot(config: ApplicationInsightsConfig): ModuleWithProviders {
    return {
      ngModule: ApplicationInsightsModule,
      providers: [
        {provide: ApplicationInsightsConfig, useValue: config}
      ]
    };
  }

  constructor(@Optional() @SkipSelf() parentModule: ApplicationInsightsModule,
              private appInsightsService: ApplicationInsightsService) {
    if (parentModule) {
      throw new Error(
        'ApplicationInsightsModule is already loaded. Import it in the AppModule only');
    }

    appInsightsService.init();
  }
}



