import { Component, EventEmitter, OnInit, Output, ViewChild, ViewEncapsulation } from '@angular/core';
import { MatMenu } from '@angular/material';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import { ApplicationInsightsService } from '../../application-insights/application-insights.service';
import { CoreService } from '../../core/core.service';
import { ThemeService } from '../../core/theme/theme.service';
import { FriggResponse, GetCurrentLoginInformationsOutput, SessionService } from '../../proxies';

@Component({
  selector: 'frigg-toolbar',
  templateUrl: 'toolbar.component.html',
  styleUrls: [ 'toolbar.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class ToolbarComponent implements OnInit {
  private userSubject: BehaviorSubject<string> = new BehaviorSubject('');
  private imageUrlSubject: BehaviorSubject<string> = new BehaviorSubject('');

  @Output() toggleSideNav: EventEmitter<any> = new EventEmitter();
  @Output() navigateHome: EventEmitter<any> = new EventEmitter();

  @ViewChild(MatMenu) menu: MatMenu;

  constructor(private themeService: ThemeService,
              public core: CoreService,
              public session: SessionService,
              private appInsigtsService: ApplicationInsightsService) {
  }

  get loggedOnUser(): Observable<string> {
    return this.userSubject.asObservable();
  }

  get userImageUrl(): Observable<string> {
    return this.imageUrlSubject.asObservable();
  }

  ngOnInit(): void {
    this.session.getCurrentLoginInformations().subscribe((response: FriggResponse<GetCurrentLoginInformationsOutput>) => {
      if (response.result) {
        this.appInsigtsService.setAuthenticatedUserContext(
          response.result.user.userName,
          `${response.result.tenant.id}|${response.result.user.id}`);
        this.userSubject.next(response.result.user.name + ' ' + response.result.user.surname);
        this.imageUrlSubject.next(response.result.user.imageUrl);
      }
    });
  }

  onToggleSideNav(): void {
    this.toggleSideNav.emit();
  }

  onNavigateHome(): void {
    this.navigateHome.emit();
  }

  onChangeLanguage(): Observable<any> {
    const newLang: string = this.core.translation.currentLang === 'nb-NO' ? 'en' : 'nb-NO';

    return this.core.translation.use(newLang);
  }

  onSelectTheme(theme: string): void {
    this.themeService.setTheme(theme);
  }

  onLogout(): void {
    this.core.authentication.logOut();
  }
}



