import { Injectable } from '@angular/core';
import { Channel, Pusher } from 'pusher-js';
import { Observable } from 'rxjs/Observable';
import { Observer } from 'rxjs/Observer';
import { environment } from '../../../../environments/environment';
import { FriggResponse, PusherConfigurationService } from '../../../proxies';
import { AuthenticationService } from '../../authentication/authentication.service';
import { NotificationMessage } from '../notification-message';
import { INotificationService } from '../notification-service';

declare const window: any;

@Injectable()
export class PusherService implements INotificationService {
  private _pusher: Pusher;

  constructor(private authenticationService: AuthenticationService,
              private pusherConfig: PusherConfigurationService) {
  }

  listen(subject: string, event: string): Observable<NotificationMessage<any>> {
    return this.getPusherInstance()
      .flatMap((pusher: Pusher) => {
        const channel: Channel = pusher
          .subscribe(subject);

        return Observable
          .create((observer: Observer<NotificationMessage<any>>) => {
            channel.bind(event, (data: any) => {
              observer.next({message: event, data});
            });
          });
      });
  }

  stopListening(subject: string): void {
    this.getPusherInstance()
      .subscribe((pusher: Pusher) => {
        pusher.unsubscribe(subject);
      });
  }

  private getPusherInstance(): Observable<Pusher | undefined> {
    if (this._pusher) {
      return Observable.of(this._pusher);
    }

    return this.pusherConfig.getAppKey()
      .map((response: FriggResponse<string>) => {
        if (response.success) {
          const appKey: string = response.result;

          this._pusher = new window.Pusher(appKey, {
            cluster: 'eu',
            encrypted: true,
            authEndpoint: environment.apiPath + '/api/services/pusher/Authorization/Authorize',
            auth: {
              headers: {
                Authorization: 'Bearer ' + this.authenticationService.getAccessToken()
              }
            }
          });

          return this._pusher;
        }
      });
  }
}



