import { AfterViewInit, Directive, ElementRef } from '@angular/core';

@Directive({
  selector: 'form[friggFocusFirst]'
})
export class SelectFirstInputDirective implements AfterViewInit {

  constructor(private el: ElementRef) {
  }

  private getInputElement(nativeElement: HTMLElement): HTMLInputElement | null {
    if (!nativeElement || !nativeElement.children) {
      return null;
    }
    if (!nativeElement.children.length && nativeElement.localName === 'input' && !nativeElement.hidden) {
      return nativeElement as HTMLInputElement;
    }

    let input: HTMLInputElement | null = null;

    [].slice.call(nativeElement.children).every((c: HTMLElement): boolean => {
      input = this.getInputElement(c);
      return !input;
    });

    return input;
  }

  ngAfterViewInit(): void {
    const formChildren: Array<HTMLElement> = [].slice.call(this.el.nativeElement.children);

    formChildren.every((child: any) => {
      const input: any = this.getInputElement(child);

      if (input) {
        input.focus();
        return false; // break!
      }

      return true; // continue!
    });
  }
}



