import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { ListResultDto } from '../frigg-response.model';
import { FriggResponse, FriggServiceHandler } from '../frigg.service.handler';

@Injectable()
export class ActionTypeService {
  constructor(private serviceHandler: FriggServiceHandler) {
  }

  getActionTypeList(): Observable<FriggResponse<ListResultDto<ActionTypeDto>>> {
    return this.serviceHandler.request('GET', 'api/services/cplan/ActionType/GetActionTypeList',
      {});
  }
}

export class ActionTypeDto {
  id: string;
  category: string;
  parameters: ActionTypeParameterDto[];
}

export class ActionTypeParameterDto {
  description: string;
  type: string;
}







