import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { ListResultDto } from '../frigg-response.model';
import { FriggResponse, FriggServiceHandler } from '../frigg.service.handler';

@Injectable()
export class LocalizationService {
  constructor(private serviceHandler: FriggServiceHandler) {
  }

  getTitles(locale: String): Observable<FriggResponse<any>> {
    return this.serviceHandler.request('GET', 'api/services/app/Localization/GetTitles?locale= ' + encodeURI(locale.toString())
      + '',
      {});
  }

  getLanguages(): Observable<FriggResponse<ListResultDto<LanguageListDto>>> {
    return this.serviceHandler.request('GET', 'api/services/app/Localization/GetLanguages',
      {});
  }
}

export class LanguageListDto {
  name: string;
  displayName: string;
  icon: string;
}







