import { Component, ContentChild, EventEmitter, Input, Output, TemplateRef, ViewChild, ViewEncapsulation } from '@angular/core';
import { VirtualScrollComponent } from 'angular2-virtual-scroll';

@Component({
  selector: 'frigg-virtual-tile-list',
  templateUrl: 'virtual-tile-list.component.html',
  styleUrls: [ 'virtual-tile-list.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class VirtualTileListComponent {
  @Input() items: any[] = [];
  @Input() allowDelete = true;
  @Output() itemClick: EventEmitter<any> = new EventEmitter();
  @Output() deleteItemClick: EventEmitter<any> = new EventEmitter();
  @ContentChild(TemplateRef) itemTemplate: TemplateRef<any>;

  @ViewChild('virtualScroll') virtualScroll: VirtualScrollComponent;

  viewPortItems: any[];

  onItemClick(item: any): void {
    if (!item.deleteFlag) {
      this.itemClick.next(item);
    }
  }

  onItemDeleteClick(item: any): void {
    this.deleteItemClick.next(item);
  }

  scrollInto(item: any): void {
    this.virtualScroll.scrollInto(item);
  }
}



