export interface FriggResponse<TResult> {
  __abp: boolean;
  success: boolean;
  result: TResult;
  error: FriggError;
  unAuthorizedRequest: boolean;
}

export interface FriggError {
  message: string;
  code?: number;
  details?: any;
  validationErrors?: any;
}

export class FriggErrorMock implements FriggError {
  message = '';
  code = 0;
  details?: any = null;
  validationErrors?: any = null;

  constructor(config?: Partial<FriggError>) {
    Object.assign(this, config);
  }
}

export class FriggResponseMock<TResult> implements FriggResponse<TResult> {
  __abp = true;
  success = true;
  result: TResult;
  error: FriggErrorMock = new FriggErrorMock;
  unAuthorizedRequest = false;

  constructor(config?: Partial<FriggResponse<Partial<TResult>>>) {
    Object.assign(this, config);
  }
}

export class ListResultDto<T> {
  items: T[];
}

export class PagedResultDto<T> {
  totalCount: number;
  items: T[];
}



