import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ValidationSchema } from 'class-validator';
import { Observable } from 'rxjs/Observable';
import { ListResultDto, PagedResultDto } from '../frigg-response.model';
import { FriggResponse, FriggServiceHandler } from '../frigg.service.handler';

@Injectable()
export class JobDefinitionService {
  constructor(private serviceHandler: FriggServiceHandler) {
  }

  create(input: JobDefinitionInput): Observable<FriggResponse<JobDefinitionDto>> {
    return this.serviceHandler.request('POST', 'api/services/cplan/JobDefinition/Create',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify(input)
      });
  }

  update(input: JobDefinitionInput): Observable<FriggResponse<JobDefinitionDto>> {
    return this.serviceHandler.request('PUT', 'api/services/cplan/JobDefinition/Update',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify(input)
      });
  }

  getList(): Observable<FriggResponse<ListResultDto<JobDefinitionListDto>>> {
    return this.serviceHandler.request('GET', 'api/services/cplan/JobDefinition/GetList',
      {});
  }

  get(id: Number): Observable<FriggResponse<JobDefinitionDto>> {
    return this.serviceHandler.request('GET', 'api/services/cplan/JobDefinition/Get?id= ' + encodeURI(id.toString())
      + '',
      {});
  }

  getAll(sorting: String, skipCount: Number, maxResultCount: Number): Observable<FriggResponse<PagedResultDto<JobDefinitionDto>>> {
    return this.serviceHandler.request('GET', 'api/services/cplan/JobDefinition/GetAll?sorting= ' + encodeURI(sorting.toString())
      + '&skipCount= ' + encodeURI(skipCount.toString())
      + '&maxResultCount= ' + encodeURI(maxResultCount.toString())
      + '',
      {});
  }

  delete(id: Number): Observable<FriggResponse<void>> {
    return this.serviceHandler.request('DELETE', 'api/services/cplan/JobDefinition/Delete?id= ' + encodeURI(id.toString())
      + '', {
      headers: new HttpHeaders({'Content-type': 'application/json'}),
      body: JSON.stringify({})
    });
  }
}

export class JobDefinitionInput {
  id: number;
  name: string;
  description: string;
  status: string;
  isTemplate: boolean;
  documents: JobDocumentInput[];
  tasks: JobTaskInput[];
}

export let JobDefinitionInputSchema: ValidationSchema = {
  name: 'JobDefinitionInputSchema',
  properties: {
    id: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }

    ]
    , name: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }
      , {
        type: 'maxLength',
        constraints: [ 50 ],
        options: []
      }

    ]
    , description: [
      {
        type: 'maxLength',
        constraints: [ 10000 ],
        options: []
      }

    ]
    , status: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }
      , {
        type: 'maxLength',
        constraints: [ 25 ],
        options: []
      }

    ]

  }
};


export class JobDefinitionDto {
  name: string;
  description: string;
  status: string;
  isTemplate: boolean;
  tasks: JobTaskDto[];
  documents: JobDocumentDto[];
  isDeleted: boolean;
  deleterUserId: number;
  deletionTime: Date;
  lastModificationTime: Date;
  lastModifierUserId: number;
  creationTime: Date;
  creatorUserId: number;
  id: number;
}

export class JobTaskDto {
  jobId: number;
  name: string;
  description: string;
  status: string;
  userId: number;
  daysToComplete: number;
  notifyByEmail: boolean;
  notifyBySms: boolean;
  deleteFlag: boolean;
  active: boolean;
  actions: JobTaskActionDto[];
  id: number;
}

export class JobTaskActionDto {
  name: string;
  description: string;
  actionType: string;
  deleteFlag: boolean;
  requiredAction: boolean;
  actionComment: number;
  enabled: boolean;
  parameters: JobTaskActionParameterDto[];
  id: number;
}

export class JobTaskActionParameterDto {
  description: string;
  value: string;
  deleteFlag: boolean;
  id: number;
}


export class JobDefinitionListDto {
  id: number;
  name: string;
  status: string;
}


export class JobDocumentInput {
  id: number;
  name: string;
  description: string;
  documentType: string;
  newRevision?: CreateJobDocumentRevisionInput;
  deleteFlag: boolean;
}

export let JobDocumentInputSchema: ValidationSchema = {
  name: 'JobDocumentInputSchema',
  properties: {
    id: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }

    ]
    , name: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }
      , {
        type: 'maxLength',
        constraints: [ 50 ],
        options: []
      }

    ]
    , description: [
      {
        type: 'maxLength',
        constraints: [ 10000 ],
        options: []
      }

    ]
    , documentType: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }
      , {
        type: 'maxLength',
        constraints: [ 25 ],
        options: []
      }

    ]

  }
};


export class CreateJobDocumentRevisionInput {
  fileContent: string;
  fileName: string;
  fileDate: Date;
  documentKey: string;
}

export class JobTaskInput {
  id: number;
  name: string;
  description: string;
  userId: number;
  daysToComplete: number;
  notifyByEmail: boolean;
  notifyBySms: boolean;
  deleteFlag: boolean;
  actions: JobTaskActionInput[];
}

export let JobTaskInputSchema: ValidationSchema = {
  name: 'JobTaskInputSchema',
  properties: {
    id: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }

    ]
    , name: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }
      , {
        type: 'maxLength',
        constraints: [ 50 ],
        options: []
      }

    ]
    , description: [
      {
        type: 'maxLength',
        constraints: [ 10000 ],
        options: []
      }

    ]
    , daysToComplete: [
      {
        type: 'min',
        constraints: [ 0 ],
        options: []
      }
      , {
        type: 'max',
        constraints: [ 2147483647 ],
        options: []
      }

    ]

  }
};


export class JobTaskActionInput {
  id: number;
  name: string;
  description: string;
  actionType: string;
  deleteFlag: boolean;
  requiredAction: boolean;
  actionComment: number;
  parameters: JobTaskActionParameterInput[];
}

export let JobTaskActionInputSchema: ValidationSchema = {
  name: 'JobTaskActionInputSchema',
  properties: {
    id: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }

    ]
    , name: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }
      , {
        type: 'maxLength',
        constraints: [ 50 ],
        options: []
      }

    ]
    , description: [
      {
        type: 'maxLength',
        constraints: [ 10000 ],
        options: []
      }

    ]
    , actionType: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }

    ]

  }
};


export class JobTaskActionParameterInput {
  description: string;
  value: string;
  id: number;
  deleteFlag: boolean;
}

export let JobTaskActionParameterInputSchema: ValidationSchema = {
  name: 'JobTaskActionParameterInputSchema',
  properties: {
    description: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }
      , {
        type: 'maxLength',
        constraints: [ 10000 ],
        options: []
      }

    ]
    , value: [
      {
        type: 'maxLength',
        constraints: [ 1000 ],
        options: []
      }

    ]
    , id: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }

    ]

  }
};


export class JobDocumentDto {
  name: string;
  description: string;
  documentType: string;
  deleteFlag: boolean;
  revisions: JobDocumentRevisionDto[];
  id: number;
}

export class JobDocumentRevisionDto {
  creatorUser: JobDocumentRevisionCreatorDto;
  creationTime: Date;
  fileName: string;
  fileDate: Date;
  documentKey: string;
  id: number;
}

export class JobDocumentRevisionCreatorDto {
  id: number;
  userName: string;
  emailAddress: string;
  name: string;
  surname: string;
}



