import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/first';
import 'rxjs/add/operator/mergeMap';
import 'rxjs/add/operator/retryWhen';
import { Observable } from 'rxjs/Observable';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import 'rxjs/observable/throw';
import { Observer } from 'rxjs/Observer';
import { environment } from '../../environments/environment';
import { AuthenticationService } from '../core/authentication/authentication.service';
import { LoadingService } from '../core/loading/loading.service';
import { ToastService } from '../core/toast/toast.service';
import { FriggError, FriggResponse } from './frigg-response.model';

interface RequestOptionsArgs {
  body?: any;
  headers?: HttpHeaders | {
    [header: string]: string | string[];
  };
  observe?: 'body';
  params?: HttpParams | {
    [param: string]: string | string[];
  };
  responseType?: 'json';
  reportProgress?: boolean;
  withCredentials?: boolean;
}

@Injectable()
export class FriggServiceHandler {
  private defaultError: FriggError = {
    message: 'Server request did not succeed!',
    details: 'Error detail not sent by server'
  };

  constructor(private httpClient: HttpClient,
              private toastService: ToastService,
              private authenticationService: AuthenticationService,
              private translate: TranslateService,
              private loading: LoadingService) {
  }

  request(method: string, url: string, options?: RequestOptionsArgs): Observable<FriggResponse<any>> {
    options = options || {};

    this.loading.start();

    return this.setAuthorizationToken(options)
      .mergeMap((authorizedOptions: RequestOptionsArgs) =>
        this.httpClient.request(method, `${environment.apiPath}/${url}`, authorizedOptions))
      .retryWhen((errors: Observable<any>) => this.handleUnauthorizedRequest(errors))
      .map((response: FriggResponse<any>) => this.handleFriggResponse(response))
      .first()
      .catch((error: any) => this.catchError(error));
  }

  private setAuthorizationToken(options: RequestOptionsArgs): Observable<RequestOptionsArgs> {
    return Observable.create((observer: Observer<RequestOptionsArgs>) => {
      options.headers = ((options.headers || new HttpHeaders()) as HttpHeaders)
        .set('Authorization', 'Bearer ' + this.authenticationService.getAccessToken())
        .set('accept-language', this.translate.currentLang);

      observer.next(options);
    });
  }


  private handleFriggResponse(response: FriggResponse<any>): FriggResponse<any> {
    if (response && !response.success) {
      if (!response.error) {
        response.error = this.defaultError;
      }

      this.toastService.error(response.error.details, response.error.message);
    }

    this.loading.complete();
    return response;
  }

  handleUnauthorizedRequest(errors: any): Observable<boolean> {
    return errors.mergeMap((error: any) => {
      const friggResponse: FriggResponse<any> = error.json ? error.json() : null;

      if (friggResponse && friggResponse.unAuthorizedRequest) {
        this.loading.complete();
        // TODO: Reauthenticate
        return false;
      } else {
        throw error;
      }
    });
  }

  catchError(error: any): ErrorObservable {
    const friggResponse: FriggResponse<any> = error.json ? error.json() : null;

    if (friggResponse && friggResponse.__abp !== undefined) {
      this.handleFriggResponse(friggResponse);
      return Observable.throw(error || 'Unable to connect to server');
    }

    this.toastService.error('Unable to connect to server');
    this.loading.complete();

    return Observable.throw(error || 'Unable to connect to server');
  }
}

export { FriggResponse, FriggError }



