import { EventEmitter, Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { CookieService } from 'ngx-cookie';
import { CordovaIFrameNavigator, CordovaPopupNavigator, Log, User, UserManager } from 'oidc-client';
import 'rxjs/add/observable/fromPromise';
import { environment } from '../../../../environments/environment';
import { AuthenticationService } from '../authentication.service';


@Injectable()
export class OidcClientService implements AuthenticationService {
  mgr: UserManager;

  userLoadedEvent: EventEmitter<User> = new EventEmitter<User>();
  currentUser: User;
  loggedIn = false;


  constructor(private router: Router,
              private translate: TranslateService,
              private cookieService: CookieService) {
    Log.logger = console;
    Log.level = Log.WARN;

    this.mgr = new UserManager({
      authority: environment.authConfig.issuer,
      client_id: environment.authConfig.clientId,
      redirect_uri: environment.authConfig.redirectUri,
      post_logout_redirect_uri: environment.authConfig.postLogoutRedirectUri,
      scope: environment.authConfig.scope,
      response_type: 'token id_token',
      automaticSilentRenew: true,
      includeIdTokenInSilentRenew: true,
      popupNavigator: environment.isCordova ? new CordovaPopupNavigator() : undefined,
      iframeNavigator: environment.isCordova ? new CordovaIFrameNavigator() : undefined
    });

    this.getUser();

    this.mgr.events.addUserLoaded((user: User) => {
      this.currentUser = user;
      if (!environment.production) {
        console.log('authService addUserLoaded', user);
      }
    });

    this.mgr.events.addUserUnloaded((e: any) => {
      if (!environment.production) {
        console.log('user unloaded');
      }
      this.loggedIn = false;
    });
  }

  get isLoggedIn(): boolean {
    return this.loggedIn;
  }

  handleLogin(route: ActivatedRouteSnapshot): void {
    if (route.url[ 0 ].path === 'login_callback') {
      this.endSigninMainWindow();
    } else {
      const returnUrl = route.queryParams[ 'returnUrl' ] || '/dashboard';
      this.startSigninMainWindow(returnUrl);
    }
  }

  getAccessToken(): string {
    return this.currentUser.access_token;
  }

  logOut(): void {
    this.startSignoutMainWindow();
  }

  private getUser(): void {
    this.mgr.getUser().then((user: User) => {
      this.currentUser = user;
      this.userLoadedEvent.emit(user);
    }).catch((err: any) => {
      console.log(err);
    });
  }

  startSigninMainWindow(returnUrl: string): void {
    this.addTenantAndCultureToCookie();
    this.mgr.signinRedirect({
      data: returnUrl
    })
      .catch((err: any) => {
        console.log(err);
      });
  }

  endSigninMainWindow(): void {
    this.mgr.signinRedirectCallback()
      .then((user: User) => {
        const returnUrl = user.state ?
          user.state : '/dashboard';

        this.currentUser = user;
        this.loggedIn = true;

        this.router.navigate([ returnUrl ]);
      }).catch((err: any) => {
      console.log(err);
    });
  }

  startSignoutMainWindow(): void {
    this.addTenantAndCultureToCookie();
    this.mgr.signoutRedirect().then()
      .catch((err: any) => {
        console.log(err);
      });
  };

  addTenantAndCultureToCookie(): void {
    this.cookieService.put('.AspNetCore.Culture', `c=${this.translate.currentLang}|uic=${this.translate.currentLang}`);
    this.cookieService.put('Abp.TenantId', '1');
  }

}



