import { Injectable, Injector } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { AuthenticationService } from './authentication/authentication.service';
import { DialogService } from './dialog/dialog.service';
import { INotificationService, NotificationService } from './notification/notification-service';
import { ShellService } from './shell/shell.service';
import { ToastService } from './toast/toast.service';

@Injectable()
export class CoreService {
  notification: INotificationService;

  constructor(injector: Injector,
              public shell: ShellService,
              public translation: TranslateService,
              public dialog: DialogService,
              public toast: ToastService,
              public authentication: AuthenticationService) {
    this.notification = injector.get(NotificationService);
  }

  setTranslatedShellHeader(key: string, interpolateParams?: Object): void {
    this.shell.setHeader(this.translation.stream(key, interpolateParams));
  }

  setTranslatedNavigateText(key: string, interpolateParams?: Object): void {
    this.shell.setNavigateText(this.translation.stream(key, interpolateParams));
  }

  translate(key: string | Array<string>, interpolateParams ?: Object): string {
    return this.translation.instant(key, interpolateParams);
  }
}



