import { Injectable } from '@angular/core';
import { ActiveToast, IndividualToastrConfig, ToastrService } from 'ngx-toastr';

@Injectable()
export class ToastService {
  constructor(private toastrService: ToastrService) {
  }

  info(message: string, title?: string): ActiveToast {
    return this.toastrService.info(message, title, this.getConfig());
  }

  error(message: string, title?: string): ActiveToast {
    return this.toastrService.error(message, title, this.getConfig());
  }

  success(message: string, title?: string): ActiveToast {
    return this.toastrService.success(message, title, this.getConfig());
  }

  warning(message: string, title?: string): ActiveToast {
    return this.toastrService.warning(message, title, this.getConfig());
  }

  getConfig(): IndividualToastrConfig {
    const toastConfig: any = {
      timeOut: 3000,
      closeButton: true
    };

    if (window.innerWidth < 600) {
      toastConfig.positionClass = 'toast-top-full-width';
    } else {
      toastConfig.positionClass = 'toast-top-right';
    }

    return toastConfig;
  }
}



