import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { FriggResponse, FriggServiceHandler } from '../frigg.service.handler';

@Injectable()
export class SystemService {
  constructor(private serviceHandler: FriggServiceHandler) {
  }

  restartActors(): Observable<FriggResponse<boolean>> {
    return this.serviceHandler.request('POST', 'api/services/cplan/System/RestartActors',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify({})
      });
  }
}






