import { Routes } from '@angular/router';
import { AuthGuard } from './core/auth-guard/auth-guard-service';
import { LoginScreenComponent } from './core/authentication/login-screen/login-screen.component';
import { CanDeactivateGuard } from './core/can-deactivate-guard/can-deactivate-guard.service';
import { NoContentComponent } from './shared/no-content';
import { ShellComponent } from './shell/shell/shell.component';

export const ROUTES: Routes = [
  {path: 'login', pathMatch: 'full', component: LoginScreenComponent},
  {path: 'login_callback', pathMatch: 'full', component: LoginScreenComponent},
  {
    path: '', component: ShellComponent,
    children: [
      {
        path: '', pathMatch: 'full', redirectTo: 'dashboard'
      },
      {
        path: 'dashboard', loadChildren: './dashboard/dashboard.module#DashboardModule',
        canActivate: [ AuthGuard ],
        canActivateChild: [ AuthGuard ],
        canLoad: [ AuthGuard ],
        canDeactivate: [ CanDeactivateGuard ]
      },
      {
        path: 'admin', loadChildren: './admin/admin.module#AdminModule',
        canActivate: [ AuthGuard ],
        canActivateChild: [ AuthGuard ],
        canLoad: [ AuthGuard ],
        canDeactivate: [ CanDeactivateGuard ]
      },
      {
        path: 'jobdefinitions', loadChildren: './job-definition/job-definition.module#JobDefinitionModule',
        canActivate: [ AuthGuard ],
        canActivateChild: [ AuthGuard ],
        canLoad: [ AuthGuard ],
        canDeactivate: [ CanDeactivateGuard ]
      },
      {
        path: 'jobcomments', loadChildren: './job-comments/job-comments.module#JobCommentsModule',
        canActivate: [ AuthGuard ],
        canActivateChild: [ AuthGuard ],
        canLoad: [ AuthGuard ],
        canDeactivate: [ CanDeactivateGuard ]
      },
      {
        path: 'tasks', loadChildren: './task/task.module#TaskModule',
        canActivate: [ AuthGuard ],
        canActivateChild: [ AuthGuard ],
        canLoad: [ AuthGuard ],
        canDeactivate: [ CanDeactivateGuard ]
      } ]
  },
  {path: '**', component: NoContentComponent}
];



