import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

@Injectable()
export class ThemeService {
  private themeKey = 'frigg-theme';

  theme: BehaviorSubject<string> = new BehaviorSubject('grey-amber');

  constructor() {
    const lastTheme: string | null = localStorage.getItem(this.themeKey);

    if (lastTheme) {
      this.theme.next(lastTheme);
    }
  }

  setTheme(theme: string): void {
    localStorage.setItem(this.themeKey, theme);
    this.theme.next(theme);
  }

  getTheme(): string {
    return this.theme.getValue();
  }
}




