import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { FriggResponse, FriggServiceHandler } from '../frigg.service.handler';

@Injectable()
export class SessionService {
  constructor(private serviceHandler: FriggServiceHandler) {
  }

  getCurrentLoginInformations(): Observable<FriggResponse<GetCurrentLoginInformationsOutput>> {
    return this.serviceHandler.request('GET', 'api/services/app/Session/GetCurrentLoginInformations',
      {});
  }
}

export class GetCurrentLoginInformationsOutput {
  application: ApplicationInfoDto;
  user: UserLoginInfoDto;
  tenant: TenantLoginInfoDto;
}

export class ApplicationInfoDto {
  version: string;
  releaseDate: Date;
  features: any;
}

export class UserLoginInfoDto {
  name: string;
  surname: string;
  userName: string;
  emailAddress: string;
  imageUrl: string;
  id: number;
}

export class TenantLoginInfoDto {
  tenancyName: string;
  name: string;
  id: number;
}







