import { CommonModule } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { SelectModule } from 'ng-select';
import { LoadingModule } from '../core/loading/loading.module';
import { ControlsModule } from './controls/controls.module';
import { FriggDirectivesModule } from './directives/directives.module';
import { FriggMaterialModule } from './frigg-material/frigg-material.module';
import { LayoutModule } from './layout/layout.module';
import { NoContentComponent } from './no-content';
import { ValuesPipe } from './pipes/values.pipe';
import { TileListModule } from './tile-list/tile-list.module';

// imports: imports the module's exports. which are usually
// declarables(components / directives / pipes) and providers.
//
// exports: exports modules AND declarables (components/directives/pipes) that other modules may want to use
// SharedModule does not use CommonModule, but does use FormsModule.
// Even so, we import/export both of these because most other modules will import SharedModule and will need them.
@NgModule({
  declarations: [
    NoContentComponent,
    ValuesPipe
  ],
  imports: [
    CommonModule,
    FormsModule,
    ControlsModule,
    ReactiveFormsModule,
    HttpClientModule,
    SelectModule,
    TranslateModule,
    LoadingModule,
    LayoutModule,
    TileListModule,
    FriggDirectivesModule,
    FriggMaterialModule
  ],
  exports: [
    CommonModule,
    FormsModule,
    ControlsModule,
    ReactiveFormsModule,
    HttpClientModule,
    SelectModule,
    TranslateModule,
    LoadingModule,
    LayoutModule,
    TileListModule,
    FriggDirectivesModule,
    FriggMaterialModule,
    NoContentComponent,
    ValuesPipe
  ]
})
export class SharedModule {
}



