import { Directive, DoCheck, ElementRef, Host, Input, OnInit, Renderer2, ViewContainerRef } from '@angular/core';
import { AbstractControl, FormGroupDirective, ValidationErrors } from '@angular/forms';
import { ValidationMessageService } from '../../../core/validation/validation-message.service';

export interface IValidationComponent {
  errorMessage: string;
  validationElement: ElementRef;
}

@Directive({
  selector: '[friggValidate]'
})
export class ValidationDirective implements DoCheck, OnInit {
  validationComponent: IValidationComponent;

  @Input() formControlName: string;

  constructor(private renderer: Renderer2,
              private validationMessageService: ValidationMessageService,
              private viewContainerRef: ViewContainerRef,
              @Host() private formGroupDirective: FormGroupDirective) {
  }

  ngOnInit(): void {
    // TODO Change this once we have a way to get hold of host component without specifying type
    this.validationComponent = (<any>this.viewContainerRef)._data.componentView.component;
  }

  ngDoCheck(): void {
    if (!this.validationComponent || !this.formControlName || !this.formGroupDirective.form) {
      return;
    }

    const formControl: AbstractControl = this.formGroupDirective.form.controls[ this.formControlName ];
    const validationElement: ElementRef = this.validationComponent.validationElement;
    const validationEl: any = validationElement.nativeElement;

    if (!validationEl) {
      return;
    }

    this.validationComponent.errorMessage = '';

    validationEl.validity.valid ?
      this.renderer.removeClass(validationEl, 'is-invalid') :
      this.renderer.addClass(validationEl, 'is-invalid');

    const errors: ValidationErrors = formControl.errors || [];

    for (const propertyName in errors) {
      if (errors.hasOwnProperty(propertyName) && formControl.touched) {
        this.validationComponent.errorMessage = propertyName;
        this.validationMessageService
          .getValidatorErrorMessage(propertyName, errors[ propertyName ])
          .subscribe((title: string) => {
            this.validationComponent.errorMessage = title || propertyName;
          });
      }
    }

    validationEl.setCustomValidity(this.validationComponent.errorMessage);
  }
}



