import { CommonModule } from '@angular/common';
import { ModuleWithProviders, NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { ControlsModule } from '../controls/controls.module';
import { FriggMaterialModule } from '../frigg-material/frigg-material.module';
import { TileListModule } from '../tile-list/tile-list.module';
import { HeaderCardTileListComponent } from './header-card-tile-list/header-card-tile-list.component';
import { HeaderCardComponent } from './header-card/header-card.component';


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    TranslateModule,
    FriggMaterialModule,
    RouterModule,
    TileListModule,
    ControlsModule
  ],
  exports: [
    HeaderCardComponent,
    HeaderCardTileListComponent
  ],
  declarations: [
    HeaderCardComponent,
    HeaderCardTileListComponent
  ]

})
export class LayoutModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: LayoutModule,
      providers: []
    };
  }
}



