import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ValidationSchema } from 'class-validator';
import { Observable } from 'rxjs/Observable';
import { ListResultDto } from '../frigg-response.model';
import { FriggResponse, FriggServiceHandler } from '../frigg.service.handler';

@Injectable()
export class TenantService {
  constructor(private serviceHandler: FriggServiceHandler) {
  }

  getTenants(): Observable<FriggResponse<ListResultDto<TenantListDto>>> {
    return this.serviceHandler.request('GET', 'api/services/app/Tenant/GetTenants',
      {});
  }

  createTenant(input: CreateTenantInput): Observable<FriggResponse<void>> {
    return this.serviceHandler.request('POST', 'api/services/app/Tenant/CreateTenant', {
      headers: new HttpHeaders({'Content-type': 'application/json'}),
      body: JSON.stringify(input)
    });
  }
}

export class CreateTenantInput {
  tenancyName: string;
  name: string;
  adminEmailAddress: string;
  connectionString: string;
}

export let CreateTenantInputSchema: ValidationSchema = {
  name: 'CreateTenantInputSchema',
  properties: {
    tenancyName: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }
      , {
        type: 'maxLength',
        constraints: [ 64 ],
        options: []
      }

    ]
    , name: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }
      , {
        type: 'maxLength',
        constraints: [ 128 ],
        options: []
      }

    ]
    , adminEmailAddress: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }
      , {
        type: 'maxLength',
        constraints: [ 256 ],
        options: []
      }

    ]

  }
};


export class TenantListDto {
  tenancyName: string;
  name: string;
  id: number;
}







