import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ValidationSchema } from 'class-validator';
import { Observable } from 'rxjs/Observable';
import { ListResultDto } from '../frigg-response.model';
import { FriggResponse, FriggServiceHandler } from '../frigg.service.handler';

@Injectable()
export class UserService {
  constructor(private serviceHandler: FriggServiceHandler) {
  }

  getUserList(): Observable<FriggResponse<ListResultDto<UserListDto>>> {
    return this.serviceHandler.request('GET', 'api/services/app/User/GetUserList',
      {});
  }

  getUser(userId: Number): Observable<FriggResponse<UserDto>> {
    return this.serviceHandler.request('GET', 'api/services/app/User/GetUser?userId= ' + encodeURI(userId.toString())
      + '',
      {});
  }

  createUser(input: UserDto): Observable<FriggResponse<UserDto>> {
    return this.serviceHandler.request('POST', 'api/services/app/User/CreateUser',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify(input)
      });
  }

  updateUser(input: UserDto): Observable<FriggResponse<UserDto>> {
    return this.serviceHandler.request('PUT', 'api/services/app/User/UpdateUser',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify(input)
      });
  }

  deleteUser(userId: Number): Observable<FriggResponse<boolean>> {
    return this.serviceHandler.request('DELETE', 'api/services/app/User/DeleteUser?userId= ' + encodeURI(userId.toString())
      + '',
      {
        headers: new HttpHeaders({'Content-type': 'application/json'}),
        body: JSON.stringify({})
      });
  }
}

export class UserDto {
  userName: string;
  name: string;
  surname: string;
  emailAddress: string;
  password: string;
  isActive: boolean;
  shouldChangePasswordOnNextLogin: boolean;
  sendActivationEmail: boolean;
  assignedRoles: AssignedRoleDto[];
  imageUrl: string;
  id: number;
}

export let UserDtoSchema: ValidationSchema = {
  name: 'UserDtoSchema',
  properties: {
    userName: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }
      , {
        type: 'maxLength',
        constraints: [ 256 ],
        options: []
      }

    ]
    , name: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }
      , {
        type: 'maxLength',
        constraints: [ 32 ],
        options: []
      }

    ]
    , surname: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }
      , {
        type: 'maxLength',
        constraints: [ 32 ],
        options: []
      }

    ]
    , emailAddress: [
      {
        type: 'isNotEmpty',
        constraints: [],
        options: []
      }
      , {
        type: 'maxLength',
        constraints: [ 256 ],
        options: []
      }

    ]
    , password: [
      {
        type: 'maxLength',
        constraints: [ 32 ],
        options: []
      }

    ]

  }
};


export class AssignedRoleDto {
  roleId: number;
  name: string;
  displayName: string;
  assigned: boolean;
}

export class UserListDto {
  name: string;
  surname: string;
  userName: string;
  fullName: string;
  emailAddress: string;
  isEmailConfirmed: boolean;
  lastLoginTime: Date;
  isActive: boolean;
  creationTime: Date;
  imageUrl: string;
  id: number;
}







