import { CommonModule } from '@angular/common';
import { ModuleWithProviders, NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { FriggDirectivesModule } from '../../shared/directives/directives.module';
import { FriggMaterialModule } from '../../shared/frigg-material/frigg-material.module';
import { ConfirmDialogComponent } from './confirm/confirm-dialog.component';
import { DialogService } from './dialog.service';
import { DiscardDialogComponent } from './discard/discard-dialog.component';
import { ExecuteActionDialogComponent } from './execute-action/execute-action-dialog.component';
import { RadioDialogComponent } from './radio/radio-dialog.component';


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    TranslateModule,
    FriggMaterialModule,
    FriggDirectivesModule
  ],
  declarations: [
    ConfirmDialogComponent,
    DiscardDialogComponent,
    RadioDialogComponent,
    ExecuteActionDialogComponent
  ],
  entryComponents: [
    ConfirmDialogComponent,
    DiscardDialogComponent,
    RadioDialogComponent,
    ExecuteActionDialogComponent
  ]
})

export class DialogModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: DialogModule,
      providers: [
        DialogService
      ]
    };
  }
}



