import { AfterContentChecked, Directive, ElementRef, HostListener } from '@angular/core';

@Directive({
  // tslint:disable-next-line
  selector: 'textarea[frigg-autosize]'
})

export class AutosizeDirective implements AfterContentChecked {
  @HostListener('input', [ '$event.target' ])
  onInput(textArea: HTMLTextAreaElement): void {
    this.adjust();
  }

  constructor(public element: ElementRef) {
  }

  ngAfterContentChecked(): void {
    this.adjust();
  }

  adjust(): void {
    this.element.nativeElement.style.overflow = 'none';
    if (this.element.nativeElement.style.maxHeight) {
      this.element.nativeElement.style.overflow =
        (this.element.nativeElement.height > this.element.nativeElement.style.maxHeight) ?
          'scrollbar' :
          'none';
    }


    this.element.nativeElement.style.height = 'auto';
    this.element.nativeElement.style.height = this.element.nativeElement.scrollHeight + 'px';
  }
}



