import { ErrorHandler, Injectable, Injector } from '@angular/core';
import * as Raven from 'raven-js';
import * as StackTrace from 'sourcemapped-stacktrace';
import { ApplicationInsightsService } from './application-insights.service';

@Injectable()
export class ApplicationInsightsErrorHandler implements ErrorHandler {
  constructor(private injector: Injector) {
  }

  handleError(error: any): void {
    if (!error.stack) {
      this.trackError(error);
    } else {
      StackTrace.mapStackTrace(error.stack, (mappedStack: any) => {
        error.mappedStack = mappedStack;
        this.trackError(error);
      });
    }

    throw error;
  }

  trackError(error: any): void {
    const appInsights = this.injector.get(ApplicationInsightsService);
    appInsights.trackException(error);
    Raven.captureException(error);

    console.log(error);
  }

}



