import { HttpClient } from '@angular/common/http';
import { LOCALE_ID, ModuleWithProviders, NgModule, Optional, SkipSelf } from '@angular/core';
import { RouterModule } from '@angular/router';
import { TranslateLoader, TranslateModule, TranslateService } from '@ngx-translate/core';
import { ToastrModule, ToastrService } from 'ngx-toastr';
import { Observable } from 'rxjs/Observable';
import { AuthGuard } from './auth-guard/auth-guard-service';
import { AuthenticationModule } from './authentication/authentication.module';
import { AuthenticationService } from './authentication/authentication.service';
import { OidcClientService } from './authentication/oidc-client/oidc-client.service';
import { CanDeactivateGuard } from './can-deactivate-guard/can-deactivate-guard.service';
import { CoreService } from './core.service';
import { DialogModule } from './dialog/dialog.module';
import { FriggTranslationLoader } from './i18n/frigg.translation.loader';
import { LoadingModule } from './loading/loading.module';
import { throwIfAlreadyLoaded } from './module-import-guard/module-import-guard';
import { NotificationModule } from './notification/notification.module';
import { ShellService } from './shell/shell.service';
import { ThemeService } from './theme/theme.service';
import { ToastService } from './toast/toast.service';
import { ValidationMessageService } from './validation/validation-message.service';
import { ValidationService } from './validation/validation.service';

declare const wallaby: any;

export function translateLoaderFactory(httpClient: HttpClient): TranslateLoader {
  if (process.env.ENV === 'test' || process.env.ENV === 'test-watch' || (typeof wallaby !== 'undefined')) {
    return {
      getTranslation(): Observable<any> {
        return Observable.of({});
      }
    };
  } else {
    return new FriggTranslationLoader(httpClient);
  }
}

@NgModule({
  imports: [
    RouterModule,
    ToastrModule,
    TranslateModule,
    AuthenticationModule,
    DialogModule.forRoot(),
    LoadingModule.forRoot(),
    NotificationModule
  ],
  exports: [
    TranslateModule
  ],
  providers: [
    AuthGuard,
    ToastService,
    TranslateService,
    CanDeactivateGuard,
    ThemeService,
    ValidationService,
    ValidationMessageService
  ]
})
export class CoreModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: CoreModule,
      providers: [
        {provide: AuthenticationService, useClass: OidcClientService},
        ToastService,
        ToastrService,
        TranslateService,
        ThemeService,
        CoreService,
        ShellService,
        CanDeactivateGuard,
        {
          provide: LOCALE_ID,
          useValue: 'nb-NO'
        }
      ]
    };
  }

  constructor(@Optional() @SkipSelf() parentModule: CoreModule,
              translate: TranslateService) {
    throwIfAlreadyLoaded(parentModule, 'CoreModule');

    // this language will be used as a fallback when a translation isn't found in the current language
    translate.setDefaultLang('en');

    // the lang to use, if the lang isn't available, it will use the current loader to get them
    translate.use('nb-NO');
  }
}



