import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { FriggResponse, FriggServiceHandler } from '../frigg.service.handler';

@Injectable()
export class DashboardService {
  constructor(private serviceHandler: FriggServiceHandler) {
  }

  getDashboardCount(): Observable<FriggResponse<DashboardCountDto>> {
    return this.serviceHandler.request('GET', 'api/services/cplan/Dashboard/GetDashboardCount',
      {});
  }

  getTaskCount(): Observable<FriggResponse<number>> {
    return this.serviceHandler.request('GET', 'api/services/cplan/Dashboard/GetTaskCount',
      {});
  }
}

export class DashboardCountDto {
  userCount: number;
  roleCount: number;
  jobCount: number;
  taskCount: number;
}







