import { SelectFirstInputDirective } from './focus/select-first-input.directive';
import { ValidationDirective } from './validation/validation.directive';

@NgModule({
  imports: [
    CommonModule
  ],
  declarations: [
    SelectFirstInputDirective,
    AutosizeDirective,
    ValidationDirective
  ],
  exports: [
    SelectFirstInputDirective,
    AutosizeDirective,
    ValidationDirective
  ]
})
export class FriggDirectivesModule {

}



