import { registerLocaleData } from '@angular/common';
/*
 * Platform and Environment providers/directives/pipes
 */
import { HttpClient } from '@angular/common/http';
import localeNb from '@angular/common/locales/nb';
import { ErrorHandler, NgModule, } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { PreloadAllModules, RouterModule } from '@angular/router';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { OAuthModule } from 'angular-oauth2-oidc';
import 'hammerjs';
import { CookieModule } from 'ngx-cookie';
import { ToastrModule } from 'ngx-toastr';
import * as Raven from 'raven-js';
import { environment } from '../environments/environment';
import { AppComponent } from './app.component';
import { ROUTES } from './app.routes';
import { ApplicationInsightsModule } from './application-insights/application-insights.module';
import { ApplicationInsightsErrorHandler, } from './application-insights/application-insigths.errorhandler';
import { CoreModule, translateLoaderFactory } from './core/core.module';
import { ENV_PROVIDERS } from './environment';
import { ProxiesModule } from './proxies/proxies.module';
import { SharedModule } from './shared/shared.module';
import { ShellModule } from './shell/shell.module';

// the second parameter 'fr' is optional
registerLocaleData(localeNb, 'nb-NO');

if (environment.production) {
  Raven
    .config('https://aeaa3680ce844f9a84c23af292b2bda6@sentry.io/123435')
    .install();
}

@NgModule({
  bootstrap: [ AppComponent ],
  declarations: [
    AppComponent
  ],
  imports: [
    OAuthModule.forRoot(),
    BrowserAnimationsModule,
    SharedModule,
    RouterModule.forRoot(
      ROUTES,
      {
        useHash: false,
        preloadingStrategy: PreloadAllModules
      }),
    CoreModule.forRoot(),
    TranslateModule.forRoot(
      {
        loader: {
          provide: TranslateLoader,
          useFactory: translateLoaderFactory,
          deps: [ HttpClient ]
        }
      }),
    ProxiesModule.forRoot(),
    ToastrModule.forRoot(),
    ShellModule,
    CookieModule.forRoot(),
    ApplicationInsightsModule.forRoot({
      instrumentationKey: environment.appInsightInstrumentationKey,
    })
  ],
  providers: [
    ENV_PROVIDERS,
    {provide: ErrorHandler, useClass: ApplicationInsightsErrorHandler}
  ]
})
export class AppModule {
}



