import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { Observable } from 'rxjs/Observable';
import { ConfirmDialogComponent } from './confirm/confirm-dialog.component';
import { ExecuteActionDialogResult } from './execute-action/execute-action-dialog-result';
import { ExecuteActionDialogComponent } from './execute-action/execute-action-dialog.component';
import { RadioDialogComponent } from './radio/radio-dialog.component';
import { RadioDialogValue } from './radio/radio-dialog.model';

export type DiscardResult = 'cancel' | 'save' | 'discard';

@Injectable()
export class DialogService {
  constructor(private dialog: MatDialog) {
  }

  confirm(...messages: string[]): Observable<boolean> {
    const dialogRef: MatDialogRef<ConfirmDialogComponent> =
      this.dialog.open(ConfirmDialogComponent, {
        disableClose: true
      });

    dialogRef.componentInstance.messages = messages;

    return dialogRef.afterClosed()
      .map((result: string) => {
        return result === 'yes';
      });
  }

  discard(...messages: string[]): Observable<DiscardResult> {
    const dialogRef: MatDialogRef<ConfirmDialogComponent> =
      this.dialog.open(ConfirmDialogComponent, {
        disableClose: true
      });

    dialogRef.componentInstance.messages = messages;

    return dialogRef.afterClosed()
      .map((result: DiscardResult) => {
        return result;
      });
  }

  executeAction(message: string, placeholder: string, actionComment: number, doneText: string): Observable<ExecuteActionDialogResult> {
    const dialogRef: MatDialogRef<ExecuteActionDialogComponent> =
      this.dialog.open(ExecuteActionDialogComponent, {
        disableClose: true
      });

    dialogRef.componentInstance.message = message;
    dialogRef.componentInstance.placeholder = placeholder;
    dialogRef.componentInstance.required = (actionComment === 2);
    dialogRef.componentInstance.canComment = (actionComment > 0);
    dialogRef.componentInstance.doneText = doneText;

    return dialogRef.afterClosed()
      .map((result: ExecuteActionDialogResult) => {
        return result;
      });
  }

  radio(message: string, radioDialogValues: RadioDialogValue[], selectedValue: any = undefined): Observable<any> {
    const dialogRef: MatDialogRef<RadioDialogComponent> =
      this.dialog.open(RadioDialogComponent, {
        disableClose: true
      });

    dialogRef.componentInstance.message = message;
    dialogRef.componentInstance.radioDialogValues = radioDialogValues;
    dialogRef.componentInstance.selectedValue = selectedValue;

    return dialogRef.afterClosed()
      .map((result: any) => {
        return result;
      });
  }
}



